body{
    background: -webkit-gradient(
        linear, 
        left top, left bottom, 
        from(rgba(0, 0, 0, 0.7)), 
        to(rgba(0, 0, 0, 0.7))
      ),url("https://cdn.pixabay.com/photo/2015/09/30/08/33/flood-965092_1280.jpg");
    background: -webkit-linear-gradient(
        rgba(0, 0, 0, 0.7), 
        rgba(0, 0, 0, 0.7)
      ),url("https://cdn.pixabay.com/photo/2015/09/30/08/33/flood-965092_1280.jpg");
    background: -o-linear-gradient(
        rgba(0, 0, 0, 0.7), 
        rgba(0, 0, 0, 0.7)
      ),url("https://cdn.pixabay.com/photo/2015/09/30/08/33/flood-965092_1280.jpg");
    background: linear-gradient(
        rgba(0, 0, 0, 0.7), 
        rgba(0, 0, 0, 0.7)
      ),url("https://cdn.pixabay.com/photo/2015/09/30/08/33/flood-965092_1280.jpg");
    background-size: cover;
}
h1 {
    text-align: center;
    color: white;
}

p {
    text-align: center;
    color: white;
}

.blueBtn {
    background: #337ab7;
    color: white;
    border-color: #337ab7;
}

.whiteText {
    color: white;
}

.blackText {
    color: black;
}
.pageHeader {
    text-align: center;
    background: transparent;
    color: white;
    font-family: 'Eczar', serif;
}
.intro {
    font-size: 2rem;
}


